import React from "react"

import Layout from "../components/layout"
import SEO from "../components/seo"

const IndexPage = () => (
  <Layout>
    <SEO title="Accueil" />
    <h1>Introduction</h1>
    <p>
    Actuellement, la France est l’un des pays les plus répressif concernant l’usage du cannabis. Bien qu’étant considéré comme une drogue douce, 
    du point de vue de la loi, le cannabis n’est pas différencié des autres drogues. Pourtant les français comptent parmi ceux qui en consomment le plus. 
    Ce constat permet aisément de rendre compte de l’ampleur de la controverse au sein même de notre société. En effet d’après les chiffres l’OFDT, 
    bien que 54% des français pense que le cannabis puisse être dangereux dès le premier usage, 60% seraient d’avis de l’autoriser sous certaines conditions.
    </p>

    <p>
    Avec l’arrivée récente du CBD dans certains commerces en France, le débat est ravivé avec de nombreux interviews et débats sur toute la sphère médiatique. 
    De manière plus générale la succession de légalisations de la consommation et/ou de la vente dans un certain nombre de pays autour du globe génère une tendance 
    mondiale.
    </p>
  </Layout>
)

export default IndexPage
